import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PendientesService } from '../../services/pendientes.service';
import { Lista } from '../../models/lista.model';
import { ListaItem } from 'src/app/models/lista-item.model';

@Component({
  selector: 'app-agregar',
  templateUrl: './agregar.page.html',
  styleUrls: ['./agregar.page.scss'],
})
export class AgregarPage implements OnInit {


  lista:Lista
  nombreItem:string = ''

  constructor( private pendienteService:PendientesService, private Actroute:ActivatedRoute) { 
    
    const listaId = this.Actroute.snapshot.paramMap.get('listaId')
    //console.log(listaId);
      
    this.lista = this.pendienteService.obtenerLista(listaId)
    //console.log(this.lista);
    

   }

  ngOnInit() {
  }

  agregarItem(){

    if(this.nombreItem.length === 0){
      return
    }

    const nuevoItem = new ListaItem( this.nombreItem )
    this.lista.items.push(nuevoItem)

    this.nombreItem = ''
    this.pendienteService.guardarStorage()

  }

  cambioCheck(item:ListaItem){
  
    const pendientes = this.lista.items.filter( itemData => !itemData.completado ).length
    if(pendientes === 0){
      this.lista.termiandaEn = new Date()
      this.lista.completada = true
    } else {
      this.lista.termiandaEn = null
      this.lista.completada = false
    }

    this.pendienteService.guardarStorage()

    console.log(this.pendienteService.listas);
  }

  borrar(i:number){
    this.lista.items.splice(i, 1)
    this.pendienteService.guardarStorage()

  }

}
